.checkoutProduct {
  display: flex;
  margin: 20px 0;
  justify-content: space-around;
  align-items: flex-start;
}
.checkoutProduct__info {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  width: 75%;
}

.checkoutProduct__button {
  background-color: #f0c14f;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  padding: 10px 15px;
  font-weight: bold;
  font-family: inherit;
  cursor: pointer;
}
.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
  margin: auto;
}
.checkoutProduct__rating {
  display: flex;
}
.checkoutProduct__quantity {
  display: flex;
  align-items: center;
  justify-content: center;
}
.checkoutProduct__quantity button {
  background-color: #f0c14f;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  padding: 10px 15px;
  font-weight: bold;
  font-family: inherit;
  cursor: pointer;
  font-size: 25px;
  margin: 0 10px;
}
@media (max-width: 1000px) {
  .checkoutProduct {
    flex-direction: column;
  }
  .checkoutProduct__info {
    width: 100%;
  }
  .checkoutProduct__button {
    margin: auto;
  }
  .checkoutProduct__rating {
    align-items: center;
    justify-content: center;
  }
}
