.payment {
  background-color: #fff;
}
.payment__container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(236, 234, 234);
}
.payment__section {
  display: flex;
  padding: 20px;
  margin: 20px;
  border-bottom: 1px solid #ccc;
}
.payment__title {
  flex: 0.2;
}
.payment__address,
.payment__items,
.payment__details {
  flex: 0.8;
}
.payment__button {
  background-color: #f0c14f;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  padding: 10px 15px;
  font-weight: bold;
  font-family: inherit;
  cursor: pointer;
}
@media (max-width: 1000px) {
  .payment__section {
    flex-direction: column;
    text-align: center;
  }
}
