.header {
  height: 80px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
  margin: 0;
  width: 100%;
}
.header__logo {
  width: 120px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 10px;
}
.header__search {
  display: flex;
  flex: 1;
  align-items: center;

  justify-content: center;
}
.header__searchInput {
  padding: 10px;
  height: 50px;
  width: 90%;
  font-size: 20px;
  border-radius: 6px 0 0 6px;
}
.header__searchIcon {
  height: 48px !important;
  width: 50px !important;
  background-color: #cd9042;
  width: 100px;
  cursor: pointer;
  border-radius: 0px 6px 6px 0;
}
.option__lineOne {
  font-size: 15px;
}
.option__lineTwo {
  font-size: 18px;
  font-weight: 800;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
  width: 25%;
  margin-right: 25px;
  color: #fff;
  height: 100%;

  visibility: visible;
}
.hamburguerMenu {
  color: #fff;
}
.nav__option {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  color: #fff;
  cursor: pointer;
  height: 100%;
  text-align: center;
}
.nav__optionbasket {
  display: flex;
  height: 100%;
  align-items: center;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
}
.nav__link {
  color: #fff;
  /* display: block; */
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__icon {
  font-size: 30px !important;
  margin: 0 10px;
  color: #fff;
}
.basketCount {
  margin: 0 10px;
  font-size: 17px;
}
.hamburguerMenu {
  display: none;
  visibility: hidden;
  cursor: pointer;
  color: #fff;
  margin: 0 6px;
}
.menuIcon {
  color: #fff;
  font-size: 50px;
}
.hidden {
  visibility: hidden;
  opacity: 0;
}
.visible {
  visibility: visible;
  opacity: 1;
}
@media (max-width: 1000px) {
  .hamburguerMenu {
    display: block;
    visibility: visible;
  }
}
@media (max-width: 1200px) {
  .header {
    position: relative;
  }
  .header__nav {
    position: absolute;
    bottom: -100%;
    z-index: -10;
    border-top: 1px solid #1f2227;
    /* height: fit-content; */
    background-color: #131921;
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .header__nav {
    display: flex;
  }
}

@media (max-width: 600px) {
  .option__lineOne {
    font-size: 12px;
  }
  .option__lineTwo {
    font-size: 15px;
    font-weight: 800;
  }
}
@media (max-width: 300px) {
  .option__lineOne {
    font-size: 10px;
  }
  .option__lineTwo {
    font-size: 12px;
    font-weight: 800;
  }
}
