.checkout {
  display: flex;
  padding: 20px;
  background: #fff;
  height: max-content;
}
.checkout__adContainer {
  display: flex;
  width: 100%;
  margin: auto;
  align-items: center;
  justify-content: center;
}
.checkout__ad {
  width: 60%;
  margin-bottom: 5px;
}
.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
.checkout__subtitle {
  text-align: center;
  font-size: 50px;
  color: #333;
}
.checkout__left {
  width: 70%;
}
.checkout__right {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
}
@media (max-width: 1000px) {
  .checkout {
    flex-direction: column;
    width: 100%;
  }
  .checkout__left {
    width: 100%;
  }
  .checkout__right {
    width: 100%;
  }
}
