.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}
.login__logo {
  object-fit: contain;
  width: 150px;
  margin: 20px auto;
}
.login__container {
  width: 400px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid rgb(165, 165, 165);
  background-color: rgb(233, 231, 231);
  padding: 20px;
}
.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.login__container > form > h5 {
  margin-bottom: 5px;
  font-size: 18px;
}
.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: #fff;
  width: 98%;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 0 5px;
  font-size: 16px;
}
.login__paragraph {
  margin-top: 15px;
  font-size: 16px;
  text-align: center;
}

.login__signInButton {
  background: #f0c14f;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  padding: 10px 15px;
  font-weight: bold;
  font-family: inherit;
  cursor: pointer;
  width: 100%;
  font-size: 20px;
}
.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  border-color: #333;
  margin-top: 10px;
  cursor: pointer;
  font-size: 16px;
}
