.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 80%;

  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}
.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}
.subtotal__button {
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 40px;
  font-size: 18px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  padding: 5px 10px;
  cursor: pointer;
  font-family: inherit;
}
@media (max-width: 1000px) {
  .subtotal {
    width: 100%;
  }
}
