.product {
  display: flex;
  flex-direction: column;
  z-index: 1;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 500px;
  min-width: 100px;
  background: #fff;
  font-size: 18px;
}
.product__rating {
  display: flex;
  width: 100%;
}
.product__rating p {
  align-items: left;
}
.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
}
.product > button {
  background-color: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  margin-top: 10px;
  color: #111;
  font-size: 18px;
  padding: 5px 10px;
  cursor: pointer;
}
.product__price {
  margin-top: 5px;
}
.product__info {
  width: 100%;
  height: 100px;

  text-align: left;
  margin: 20px;
}
@media (max-width: 1000px) {
  .product__info {
    text-align: center;
  }
  .product__rating {
    width: auto;
  }
  .product__rating p {
    align-items: center;
  }
}
@media (max-width: 600px) {
  .product {
    font-size: 14px;
  }
}
